import './App.css'
import { BrowserRouter as Router, Route, Switch} from 'react-router-dom'; 
import CssQuestions from './Component/quizapp/CssQuestions';
import HtmlQuestions from './Component/quizapp/HtmlQuestions';
import JavaScriptQuestions from './Component/quizapp/JavaScriptQuestions';
import SelectedTopics from './Component/quizapp/SelectedTopics';
import Result from './Component/quizapp/Result';
import React from 'react'
import Singin from './Component/FirebaseLogin/auth/Singin';
import SignUp from './Component/FirebaseLogin/auth/Signup';
import PrivateRoute from './Component/FirebaseLogin/auth/PrivateRoute';
import { AuthProvider } from './Component/FirebaseLogin/AuthContext';



export default function App() {
  return (
    <div>
      <AuthProvider>
      <Router>
        <Switch>
          <Route path="/" exact component={Singin} />
          <Route path="/register" component={SignUp} />
          <PrivateRoute path="/SelectedTopics" exact component={SelectedTopics} />
          <PrivateRoute path="/html" component={HtmlQuestions} />
          <PrivateRoute path="/css" component={CssQuestions} />
          <PrivateRoute path="/javascript" component={JavaScriptQuestions} />
          <PrivateRoute path="/result" component={Result} />
        </Switch>
    </Router>
    </AuthProvider>
    </div>
  )
}
