import React, { useState } from 'react';
import { auth } from '../AuthFirebase';
import { signInWithEmailAndPassword } from 'firebase/auth';
import { sendPasswordResetEmail } from 'firebase/auth';
import { useHistory } from "react-router-dom";
import "./signin.css"

const Signin = () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [errorMessage, setErrorMessage] = useState('');
    const [isForgotPasswordClicked, setIsForgotPasswordClicked] = useState(false);
    const history = useHistory();


    const handleRegister = () => {
        history.push("/register");
      };

      const handleForgotPassword = () => {
        sendPasswordResetEmail(auth, email)
          .then(() => {
            setErrorMessage('Password reset email sent. Check your inbox.');
          })
          .catch((error) => {
            setErrorMessage('Password reset failed. Please try again.');
            console.error(error);
          });
    };
    

    const signIn = (e) => {
        e.preventDefault();
        signInWithEmailAndPassword(auth, email, password)
            .then(async (userCredentials) => {
                console.log(userCredentials);
                setErrorMessage('Login successful!');
                history.push(`/SelectedTopics`);
            }).catch(async (error) => {
                console.log(error);
                setErrorMessage('Login failed. Please check your credentials.')
                setIsForgotPasswordClicked(true)
            })
    }

    return (
        <div className='sign-in-container'>
            <form onSubmit={signIn}>
                <h1>QUIZ CONTEST</h1>
                <input type='email' placeholder='Enter your email' value={email} onChange={(e) => setEmail(e.target.value)} />
                <input type='password' placeholder='Enter your password' value={password} onChange={(e) => setPassword(e.target.value)} />
                <br></br><button type="submit">Log In</button>
                <button id="isRegister" onClick={handleRegister}>Register</button>
                <p id="errorMessage">{errorMessage}</p>
                {isForgotPasswordClicked &&<button id="isForgotPassword" onClick={handleForgotPassword}>Forgot Password</button>}
                
            </form>
        </div>
    )
}

export default Signin;
