import { create } from 'zustand';



const useStore = create((set) => ({
  selectedAnswers: {},

  fetchQuestions: async () => {
    const response = await fetch('https://ap-south-1.aws.data.mongodb-api.com/app/onlineexamination-dbaey/endpoint/getallquestion');
    const data = await response.json();
    set({ questions: data });
  },

  selectAnswer: (questionId, selectedOption) => {
    set((state) => (
      {
        selectedAnswers:
        {
          ...state.selectedAnswers, [questionId]: selectedOption,
        },
      }));
  },

  calculateScore: () => {
    const questions = useStore.getState().questions;
    const selectedAnswers = useStore.getState().selectedAnswers;

    const score = questions.reduce((acc, question) => {
      const selectedOption = selectedAnswers[question._id];
      if (selectedOption === question.answer) {
        return acc + 1;
      }
      return acc;
    }, 0);

    return score;
  },
}));


export default useStore;
