import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import useStore from './FetchData';
import "../quizapp/Styles.css";
import { Link } from 'react-router-dom/cjs/react-router-dom.min';
import { useAuth } from '../../Component/FirebaseLogin/AuthContext'; 

const Result = (props) => {
  const { user } = useAuth();
  const questions = useStore((state) => state.questions);
  const selectedAnswers = useStore((state) => state.selectedAnswers);
  const fetchQuestions = useStore((state) => state.fetchQuestions);
  const calculateScore = useStore((state) => state.calculateScore);

  const location = useLocation();
  const searchParams = new URLSearchParams(location.search);
  const currentTopic = searchParams.get('topic');
  const filteredQuestions = questions.filter(question => question.topic_name === currentTopic);

  const [showResults, setShowResults] = useState(false);
  const [showSubmitButton, setShowSubmitButton] = useState(true);
  const [showLink, setshowLink] = useState(false);

  useEffect(() => {
    fetchQuestions();
  },);
  

  const handleSubmitQuiz = () => {

    setShowResults(true);
    setshowLink(true)
    setShowSubmitButton(false);
  };

  return (
    <div id="resultbody">
      <div id='linkpadding1' align="center">
        {showSubmitButton && (
          <button class="button1" onClick={handleSubmitQuiz}><b>Submit</b></button>
        )}
      </div>
      <div>
        {showResults && (
          <div className="results"> 
            <h2><b><u>Result  </u> &nbsp;:- </b></h2>
            {user && (
              <p><b><u>User Email </u> &nbsp;: {user.email}</b></p>
            )}
            {filteredQuestions.map((question, index) => (
              <div key={question._id}>
                <p><b>{index + 1}. </b>{question.question_name}</p>
                <div>
                  <ol type="1">
                    <li>{selectedAnswers[question._id] === "1" ? "" : ""} {question.option1}</li>
                    <li>{selectedAnswers[question._id] === "2" ? "" : ""} {question.option2}</li>
                    <li>{selectedAnswers[question._id] === "3" ? "" : ""} {question.option3}</li>
                    <li>{selectedAnswers[question._id] === "4" ? "" : ""} {question.option4}</li>
                  </ol>
                </div>
                <div id="correctAnswer">
                  <b>Correct Answer : {question.answer}</b>
                </div>
              </div>
            ))}
            <p align="center" id="score"><b><h1><u>Total Score</u>&nbsp;: {calculateScore()} / {filteredQuestions.length}</h1></b></p>
          </div>
        )}
      </div>
      <div id='submitbutton'>
       {showLink && ( <Link to="/SelectedTopics" className='button5'>BACK</Link>)}
      </div>
    </div>
  );
};

export default Result;
