import React, { useEffect, useState } from 'react';
import useStore from './FetchData';
import "../quizapp/Styles.css";



const CssQuestions = ({ history }) => {
  const questions = useStore((state) => state.questions);
  const fetchQuestions = useStore((state) => state.fetchQuestions);
  const selectAnswer = useStore((state) => state.selectAnswer);

  const [currentTopic] = useState('css');

  useEffect(() => {
    fetchQuestions();
  });

  const handleOptionSelect = (questionId, selectedOption) => {
    selectAnswer(questionId, selectedOption);
  };


  const handleSubmit = () => {
    history.push(`/result?topic=css`);
  };





  const filteredQuestions = questions.filter(question => question.topic_name === currentTopic);

  return (
    <div id="questionsmarinbody">
      <div>
        <ul>
          <h1 align="center"><b><u>{currentTopic.toUpperCase()} QUIZ</u></b></h1>
          {filteredQuestions.map((question) => (
            <li key={question._id} id="questions" type={1}>
              <h1 id="questions">{question.question_name}</h1>
              <label >
                <input type="radio" name={`question_${question._id}`} value="1" onChange={() => handleOptionSelect(question._id, "1")} />
                &nbsp;{question.option1}<br></br>
                <input type="radio" name={`question_${question._id}`} value="1" onChange={() => handleOptionSelect(question._id, "2")} />
                &nbsp;{question.option2}<br></br>
                <input type="radio" name={`question_${question._id}`} value="1" onChange={() => handleOptionSelect(question._id, "3")} />
                &nbsp;{question.option3}<br></br>
                <input type="radio" name={`question_${question._id}`} value="1" onChange={() => handleOptionSelect(question._id, "4")} />
                &nbsp;{question.option4}
              </label>
            </li>))}
        </ul>
      </div>
      <div id='submitbutton'>
        <button className='button5' onClick={handleSubmit}>NEXT</button>
      </div>
    </div>
  );
};

export default CssQuestions;
