import React from 'react';
import { Link } from 'react-router-dom';
import "../quizapp/Styles.css";
import AuthDeatils from '../FirebaseLogin/auth/AuthDeatils';

const SelectedTopics = (props) => {

  return (
    <div id="questionsbody">

      <div>
        <div>
          <div>
            <div align="center" className="topictable">
              <div>
                   <AuthDeatils Authuser={props.Authuser} UserSignOut={props.UserSignOut} />
              </div>
              <table id="table" border="40px" cellSpacing="30" cellPadding="50">
                <tbody>
                  <tr id="topics">
                    <th className="rounded-cell">
                      <Link to="/html" className="btn_name">HTML</Link>
                    </th>
                  </tr>
                  <tr id="topics">
                    <th className="rounded-cell">
                      <Link to="/css" className="btn_name">CSS</Link>
                    </th>
                  </tr>
                  <tr id="topics">
                    <th className="rounded-cell">
                      <Link to="/javascript" className="btn_name">JAVASCRIPT</Link>
                    </th>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SelectedTopics;
